body {
    font-size: 18px;
    font-family: "PT Sans", "Arial", sans-serif;
    padding: 0;
    margin: 0;
    min-width: 320px;
}

img {
    max-width: 100%;
    height: auto;
}
  
a {
    text-decoration: none;
    &:focus {
        outline: none;
    }
}

ul {
    list-style: none;
}

ol {
    list-style: none;
}
